// стандартні стилі для кнопки, якщо ваша кнопка напівпрозора, треба дописати їй клас і значення background: rgba(0, 0, 0, 0.4); і змінити колір бордера при ховері на border: 2px solid #FFFFFF;
.button {
  display: block;
  border-radius: 25px;
  background: $black;
  font-family: inherit;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.02em;
  color: $accentYellow;
  border: 2px solid #ffffff;
  outline: none;
  cursor: pointer;
  transition: background 250ms linear, border 250ms linear;
  &:hover,
  &:focus {
    background: $white;
    border: 2px solid #f3c653;
  }
}
.link {
  padding: 8px;
  font-size: 14px;
  line-height: 1.21;
  letter-spacing: 0.02em;
  color: $white;
  border: 1px solid rgba(243, 198, 83, 0.7);
  border-radius: 2px;
}

.brand-name{
  font-family: 'Film P3';
font-style: normal;
font-weight: 700;
font-size: 40px;
line-height: 1.075;
letter-spacing: 0.3em;
color: #494848;
text-transform: uppercase;
@include forTabletUp {
  font-size: 64px;
line-height: 1.078;
}
}
.form-button{
  display: block;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  margin-top: 40px;
  background: $black;
border: 1px solid $accentYellow;
border-radius: 2px;
font-family: 'Montserrat';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 1.2;
color: $white;
text-transform: uppercase;
border: none;
outline: none;
cursor: pointer;
 transition: background 250ms linear, color 250ms linear;
&:hover, 
&:focus{
background-color: $accentYellow;
color: $black;
}
}